import React, {useState, useEffect, useMemo} from 'react';
import Card1 from '../../../images/Cart-Confirmation/cards/card1.webp'
import Card2 from '../../../images/Cart-Confirmation/cards/card2.webp'
import Card3 from '../../../images/Cart-Confirmation/cards/card3.webp'
import Card4 from '../../../images/Cart-Confirmation/cards/card4.webp'
import YapeLogo from '../../../images/Cart-Confirmation/cards/channels4_profile.jpg';
import PlinLogo from '../../../images/Cart-Confirmation/cards/PLIN.png';
import {useNavigate} from "react-router-dom";
import {FaCheck} from 'react-icons/fa'
import {PATH_PREFIX_BACKEND} from "../../../Data/EnvironmentVariables"
import NotificationBandSection from '../../NotificationBand';
import axios from 'axios';
import LoadingAsync from '../../LoadingAsync';


import {ContainerPayment, ContainerWrapper, Column1, Column2, Header, Description, Account, ButtonAccess, PaymentMethod, TotalImport, CardsLabel, ImportantLabel, ImportantLabelDescription, InputOption, PrivateInformationDetails, PrevDescription, ButtonReserve, Reserve, Title, ReserveDetails, RowDetail, ConceptDetail, DescriptionDetail, PriceDetails, RowPriceDetail, SmallLabelPrice, ConceptPriceDetail, DiscountPriceLabel, DiscountPrice, PaymentTotalLabel, PaymentTotalAmount, CardsRow, CardsRowItem, FotterColumn1, Optiondiv, OptionLabel, OptionRow, ArrowDownIcon, ArrowUpIcon, RowPriceDetailTotal, CardYape, CardPlinItem, ColumnDescriptionDetail} from './CartThirdSectionElements';



export const CartThirdSection = ({token, user, cart, setPropCart}) => {

  let navigate = useNavigate();

  const [popState, setPopState] = useState(false);
  const [openDetailReserve, setOpenDetailReserve] = useState(false);
  const triggerReserve = () => {
    setOpenDetailReserve(!openDetailReserve);
  }

  //Create Reserves
  const [saleData, setSaleData] = useState({});


  const [selectedPromotion, setSelectedPromotion] = useState({});

  useEffect(() => {
    setSelectedPromotion(JSON.parse(localStorage.getItem("selectedPromotion")))
  }, [])

  useEffect(() => {
    if (cart.idPromotion === 0) {
      var tentsSale = {};
      for (let tent of cart.idtents) {
        tentsSale["idtent" + tent.id] = tent.id
      }
      var experiencesSale = {};
      for (let experience of cart.experiences) {
        var id_experience = experience.id.substring(1,)
        id_experience = Number(id_experience)
        experiencesSale["idexperience" + id_experience] = {
          id: id_experience,
          qtyexperience: experience.qtyExperience
        }
      }

      var productsSale = {};
      for (let product of cart.products) {
        var id_product = product.id.substring(1,)
        id_product = Number(id_product)
        productsSale["idproduct" + id_product] = {
          id: id_product,
          qtyproduct: product.qtyProduct
        }
      }

      setSaleData({
        qtypeople: cart.qtyAdults,
        qtyKids: cart.qtyKids,
        idtents: tentsSale,
        idproducts: productsSale,
        idexperiences: experiencesSale,
        Aditionalpeople: cart.AditionalPeople,
        dateFrom: cart.checkInDate.toLocaleDateString("fr-CA"),
        dateTo: cart.checkOutDate.toLocaleDateString("fr-CA"),
        dateSale: new Date().toLocaleDateString("fr-CA"),
        idpromotion: 0
      })
    } else {

      setSaleData({
        dateFrom: cart.checkInDate.toLocaleDateString("fr-CA"),
        dateTo: cart.checkOutDate.toLocaleDateString("fr-CA"),
        dateSale: new Date().toLocaleDateString("fr-CA"),
        idpromotion: cart.idPromotion
      })
    }
  }, [cart])

  const [callAPIReserve, setCallAPIReserve] = useState(false);
  const [accessoAPI, setAccesoAPI] = useState({tipo: 'POST', url: PATH_PREFIX_BACKEND + '/api/reserves/create/'});
  const [errorAPI, setErrorAPI] = useState(null);
  const [respuestaAPI, setRespuestaAPI] = useState(null);
  const [TermsAndConditions, setTermsAndConditions] = useState(null);

  const TriggerReserve = (event) => {
    event.preventDefault();

    var inputBox = document.getElementById("CC")
    if (inputBox.checked) {
      setCallAPIReserve(true);
    } else {
      setTermsAndConditions("Acepta los terminos y condiciones");
    }
  }

  const [userNotLogIn, setUserNotLogIn] = useState(false);

  useEffect(() => {
    if (callAPIReserve === true) {
      if (user !== null) {
        const consultaAPI = async () => {
          setErrorAPI(null);
          const headers = {};
          headers['Authorization'] = token;
          try {
            const consulta = await axios({method: accessoAPI.tipo, url: accessoAPI.url, data: saleData, headers: headers})
            await setRespuestaAPI(consulta.data);
            await triggerConfirmation();
            await setPopState(false)
          } catch (error) {
            await setErrorAPI(error.response.data)
            await setPopState(false)
          }
        };
        setPopState(true)
        consultaAPI();
        setCallAPIReserve(false)
      } else {
        setCallAPIReserve(false);
        setUserNotLogIn(true);
      }
    }
  }, [callAPIReserve, saleData, user])


  const triggerConfirmation = (event) => {
    navigate('/cart-confirmation');
  }

  /*******************************NOTIFCATION VARIABLES************************************************/
  const [typeNotification, setTypeNotification] = useState("Success");
  const [MessageNotification, setMessageNotification] = useState("No Message");
  const [TriggerNotification, setTriggerNotification] = useState(false);

  const AppearNotification = async () => {
    await new Promise((r) => setTimeout(r, 4000));
    await setTriggerNotification(false);
  };

  useEffect(() => {
    if (errorAPI !== null) {
      setTypeNotification("Error");
      setMessageNotification(errorAPI.status);
      setTriggerNotification(true);
      AppearNotification();
      setErrorAPI(null);
    } else if (TermsAndConditions !== null) {
      setTypeNotification("Error");
      setMessageNotification(TermsAndConditions);
      setTriggerNotification(true);
      AppearNotification();
      setTermsAndConditions(null);
    } else if (userNotLogIn !== false) {
      setTypeNotification("Error");
      setMessageNotification("Debes estar Logeado para poder realizar tu reserva");
      setTriggerNotification(true);
      AppearNotification();
      setUserNotLogIn(false);
    }
  }, [errorAPI, TermsAndConditions, userNotLogIn])

  return (
    <ContainerPayment>
      <LoadingAsync Open={popState} />
      <NotificationBandSection type={typeNotification} message={MessageNotification} trigger={TriggerNotification} />
      <ContainerWrapper>
        <Column1>
          <Account>
            {user ?
              <>
                <Header>Hola {user.name}</Header>
                <Description>Ya esta todo listo para tu reserva proced a confirmar</Description>
              </>
              :
              <>
                <Header>Ya tienes una cuenta?</Header>
                <Description>Si ya tienes una cuenta Bambucamp Glamping accede ahora para finalizar tu reserva.</Description>
                <ButtonAccess to="/signin">Acceder</ButtonAccess>
              </>
            }

          </Account>
          <PaymentMethod>
            <Header>Mtodo de Pago</Header>
            <Description>Si tienes algn tipo de duda contacta con nuestro numero  001-305-593-4043 o envia un mail a reservationsbpusa@bahia-principe.com o escribenos a nuestro whatsapp.</Description>

            {cart.idPromotion !== 0 ? <TotalImport>{selectedPromotion.PAYAMOUNTTOTAL}</TotalImport> : <TotalImport>{cart.AmountTotal}</TotalImport>}
            <CardsLabel>Aceptamos todo los pagos mediante transferencia bancaria de cualquier banco, puedes realizar el abono a la siguiente cuenta De Banco Continental: <br /> <br />
              CTA empresarial  De crialca sac<br />
              00110341510200476632
              <br /> CCI<br /> 01134100020047663251</CardsLabel>
            <CardsRow>
              <CardsRowItem src={Card1}></CardsRowItem>
              <CardsRowItem src={Card2}></CardsRowItem>
              <CardsRowItem src={Card3}></CardsRowItem>
              <CardsRowItem src={Card4}></CardsRowItem>
            </CardsRow>
            <CardsLabel>Aceptamos en transferencia por Yape y Plin en PEN</CardsLabel>
            <CardsRow>
              <CardYape src={YapeLogo}></CardYape>
              <CardPlinItem src={PlinLogo}></CardPlinItem>
            </CardsRow>
            <ImportantLabel>Recuerda:</ImportantLabel>
            <ImportantLabelDescription>
              IMPORTANTE: Por favor, recuerda que en el check-in debes presentar la tarjeta de crdito con la que has hecho el pago de la reserva, as como tambin tu pasaporte, ya que el pago ser verificado en el hotel.
            </ImportantLabelDescription>
            {/*<PaymentMethodForm>
              <PaymentName />
              <CardNumber />
              <MonthNumber>
                <option value="none" selected hidden>Mes</option>
                {months.map((item) => (
                  <option value={item}>{item}</option>
                ))}
              </MonthNumber>
              <YearNumber>
                <option value="none" selected hidden>Ao</option>
                {year.map((item) => (
                  <option value={item}>{item}</option>
                ))}
              </YearNumber>
              <CVVNumber maxlength="3" />
              <PaymentAdress />
              <PaymentCity />
              <PaymentCountry id="PyCountry">
                <option value="none" selected hidden>Pais</option>
                {options.map((item) => (
                  <option value={item.value} >{item.label}</option>
                ))}
              </PaymentCountry>
              <PaymentProvidence />
              <PaymentZip />
              <PaymentPhoneNumber />
            </PaymentMethodForm>*/}
          </PaymentMethod>
          <Optiondiv>
            <OptionRow>
              <InputOption id="CC" />
              <OptionLabel>He leido y acepto las condiciones generales y la poltica de privacidad .</OptionLabel>
            </OptionRow>
            {/*<OptionRow>
              <InputOption id="CD" />
              <OptionLabel>No deseo recibir comunicaciones comerciales .</OptionLabel>
            </OptionRow>
            <OptionRow>
              <InputOption id="TC" />
              <OptionLabel>Consiento el tratamiento de mis datos para la elaboracin de perfiles para la personalizacin de ofertas y servicios.</OptionLabel>
            </OptionRow>
              */}
          </Optiondiv>
          <PrivateInformationDetails>
            <b>Informacin bsica sobre privacidad para Reservas</b> <br /><br />
            <b>Responsable:</b> BP USA TRAVEL INC. (en adelante, BP USA), no. 2, CR1102642893 y, MANAGEMENT HOTELERO PIERO S.L., con C.I.F. B57211732 (en adelante MHP)
            <br /><b>Finalidad:</b> BP USA tratar sus datos para la gestin de la reserva. MHP tratar sus datos para la gestin y mejora de la calidad y de los procesos a nivel de grupo, la gestin comercial de las marcas del grupo, el envo de comunicaciones comerciales, la elaboracin de perfiles comerciales y la personalizacin de los servicios para todas las empresas y marcas del Grupo Piero.
            <br /><b>Derechos:</b> Tiene derecho a acceder, rectificar y suprimir los datos, a limitar su tratamiento, a la portabilidad de sus datos, a oponerse al tratamiento, a presentar una reclamacin ante la autoridad de control competente, as como oponerse a la utilizacin de sus datos para la elaboracin de perfiles.
            <br /><b>Informacin adicional:</b> Puede consultar informacin adicional y detallada sobre Proteccin de Datos en la poltica de privacidad general de nuestra web disponible en www.bahia-principe.com Puede contactar con el Delegado de Proteccin de Datos de Grupo Piero en: dpd.privacy@grupo-pinero.com.
          </PrivateInformationDetails>
          <PrevDescription>Antes de enviar este formulario, debe leer la Informacin bsica sobre proteccin de datos para reservas que se encuentra arriba. Al pulsar el botn de envo manifiesta haber ledo esta informacin.</PrevDescription>
          <ButtonReserve onClick={(event) => {TriggerReserve(event)}}>Reservar Ahora</ButtonReserve>
          <FotterColumn1><b><FaCheck /> Mejores Precios</b> Garantizados </FotterColumn1>
        </Column1>
        <Column2>
          <Reserve onClick={triggerReserve}>
            <Title>Reserva</Title>
            {openDetailReserve ? <ArrowUpIcon /> : <ArrowDownIcon />}
          </Reserve>
          <ReserveDetails open={openDetailReserve}>
            <RowDetail>
              <ConceptDetail>Destino</ConceptDetail>
              <DescriptionDetail>Bambucamp Glamping</DescriptionDetail>
            </RowDetail>
            <RowDetail>
              <ConceptDetail>Fechas</ConceptDetail>
              <DescriptionDetail>del {cart.checkInDate.toLocaleDateString("fr-CA")} al {cart.checkOutDate.toLocaleDateString("fr-CA")}</DescriptionDetail>
            </RowDetail>
            {cart.idPromotion !== 0 ?
              <>
                <RowDetail>
                  <ConceptDetail>Promocion</ConceptDetail>
                  <DescriptionDetail>{selectedPromotion.DESCRIPTION}</DescriptionDetail>
                </RowDetail>
              </>
              :
              <>
                <RowDetail>
                  <ConceptDetail>Habitacin(es)</ConceptDetail>
                  <ColumnDescriptionDetail>
                    {cart.idtents.length !== 0 && (
                      cart.idtents.map((item) => {
                        return (
                          <DescriptionDetail>{item.Title}</DescriptionDetail>
                        )
                      }))}
                  </ColumnDescriptionDetail>
                </RowDetail>
                <RowDetail>
                  <ConceptDetail>Rgimen</ConceptDetail>
                  <DescriptionDetail>Todo Incluido <br />{cart.qtyAdults} Adultos {cart.qtyKids ? " y " + cart.qtyKids + " nios" : ""}</DescriptionDetail>
                </RowDetail>
              </>
            }
          </ReserveDetails>
          <PriceDetails>
            {cart.idPromotion !== 0 ?
              <></> :
              <>
                <Reserve>
                  <Title>Precio Detallado</Title>
                </Reserve>
                {cart.idtents.length !== 0 && (
                  cart.idtents.map((item) => {
                    return (
                      <RowPriceDetail>
                        <SmallLabelPrice>Habitacin</SmallLabelPrice>
                        <ConceptPriceDetail>{item.priceTent}</ConceptPriceDetail>
                      </RowPriceDetail>
                    )
                  }))}
                {cart.experiences.map((item) => (
                  <RowPriceDetail>
                    <SmallLabelPrice>{item.titleExtra}</SmallLabelPrice>
                    <ConceptPriceDetail>{item.qtyExperience * item.unitprice}</ConceptPriceDetail>
                  </RowPriceDetail>
                ))}
                {cart.products.map((item) => (
                  <RowPriceDetail>
                    <SmallLabelPrice>{item.titleExtra}</SmallLabelPrice>
                    <ConceptPriceDetail>{item.qtyProduct * item.unitprice}</ConceptPriceDetail>
                  </RowPriceDetail>
                ))}
              </>
            }
            {cart.idPromotion !== 0 ?
              <>
                <Reserve>
                  <Title>Descuentos</Title>
                </Reserve>
                <RowPriceDetail>
                  <DiscountPriceLabel>Descuento de Promocin</DiscountPriceLabel>
                  <DiscountPrice>{selectedPromotion.DISCOUNT}</DiscountPrice>
                </RowPriceDetail>
                <RowPriceDetail>
                  <PaymentTotalLabel>Total</PaymentTotalLabel>
                  <PaymentTotalAmount>{selectedPromotion.PAYAMOUNTTOTAL}</PaymentTotalAmount>
                </RowPriceDetail>
              </> :
              <>
                <RowPriceDetailTotal>
                  <PaymentTotalLabel>Total</PaymentTotalLabel>
                  <PaymentTotalAmount>{cart.AmountTotal}</PaymentTotalAmount>
                </RowPriceDetailTotal>
              </>
            }
          </PriceDetails>

        </Column2>
      </ContainerWrapper>
    </ContainerPayment >
  )
}

